






















import { Vue, Component } from 'vue-property-decorator';
import { Account } from '@/models/Account';
import API from '@/services/API';

@Component
export default class InputAmount extends Vue {
  private amount: string = '';
  private error: null | string = null;
  private valid: boolean = false;

  private get account(): Account {
    return this.$store.getters.account;
  }

  private amountRules = [
    (v: string) => v !== '' || '',
    (v: string) => Number(v) <= Number(this.account.total) || '',
    // (v: number) => v === 0 || '',
    // (v: number) => v < 0 || '',
  ];

  // private back(): void {
  //   this.$store.commit('newTransfer');
  // }

  private exec(): void {
    this.$store.commit('setProgress');
    this.$store.commit('setTransferAmount', Number(this.amount));
    this.$store.dispatch('execTransfer')
      .then(() => {
        // this.$store.commit('clearProgress');
        this.$router.push('/statements');
      })
      .catch((err) => {
        alert(err);
      });
  }
}
